import React, { useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";

const ProxyComp = () => {
  const history = useHistory();
  const { path, sessionId, status } = useParams();

  useEffect(() => {
    setTimeout(() => {
      history.replace(`/${path}/${sessionId}/${status}`);
    }, 500);
    console.log("RENDERED");
  }, [path, history, sessionId, status]);

  return <div></div>;
};

export default ProxyComp;
